import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo_default from '../../static/logo/logo_default.svg'

const Nav = styled.nav`
    position: relative;
    font-weight: 400;
    font-size: 1.2rem;
    height: 50px;
    background: transparent;
    float: right;
    direction: rtl;
    margin-right: 25%;
    a {
        position: relative;
        font-family: "Domine";
        color: #ac8d64;
        transition: all 0.25s ease-out
        width: 100%;
        display: table;
        overflow: hidden;
        div {
            position: relative;
            height: 1px;
            width: 0%;
            top: 0px;
            left: 50%;
            background-color: #dc2830;
            transition: all 0.45s ease-out;
        }
        &:hover>div {
            width: 100%;
            left: 0%;
            background-color: #dc2830;
            transition: all 0.45s ease-out;
        }
    }
`
const Logo = styled.img`
    position: relative;
    width: 100%;
    top: 0px;
`

const Pinned = function() {
};

const unPinned = function() {
};

const unFixed = function() {
};

const NavBar = () => (
    <Headroom id="main_headroom" disableInlineStyles
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={0} pinAnchor={0} hideAnchor={150} >
        <Logo id="logo_default" src={logo_default} alt='Dumna Niepodległa Logo' />
        <Nav id="nav">  
            <Link to='/'>
                Wydarzenia
                <div />
            </Link>
            <Link to='/'>
                O nas
                <div />
            </Link>
            <Link to='/'>
                Kontakt
                <div />
            </Link>
            <Link to='/'>
                Sklep
                <div />
            </Link>
        </Nav>
    </Headroom>
);

export default NavBar